@import 'variables/colors.scss';
@import 'variables/general.scss' ;

/* Portfolio */

section.portfolio{
    background-color: #1d1c1f;
}
div.title{
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    padding-bottom: 6rem 0 5rem 0 ;
}
div.exapmles {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 1em 0;
}

.portfolio img{
    padding: 1em 0;
    opacity: 0.7;
}

.portfolio img:hover{
    opacity: 1;
}
